.popup--back{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 50;
}

.popup--content {
    width: 60%;
    height: 400px;
    background-color: white;
    border-radius: 8px;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.popup--close {
    font-size: 30px;
    font-weight: bold;
    position: absolute;
    right: 20px;
    cursor: pointer;
}